

import React from 'react';
import EquipoTable from '../../presentational/EquipoTable';
import getListaHOC from '../../hoc/ListaHOC';
import EquipoForm from '../../presentational/form/EquipoForm';
const busquedad = [
        { nombre : 'Serie' , type : 'serie'},
        { nombre : 'Estado' , type : 'estado_equipo'},
        { nombre : 'Proveedor' , type : 'proveedors.razon_social'},
        { nombre : 'Tipo de Equipo' , type : 'tipo_equipos.nombre'},
        { nombre : 'Modelo' , type : 'modelos.nombre'},
    ];


const ListaEquipo = props =>  (<EquipoTable {...props} />);
export default getListaHOC(ListaEquipo,busquedad,{
    uri : '/mantenimiento/equipo/lista/',
    name : 'Equipo',
    enlace : 'equipo'
},
<EquipoForm />);



