import React from 'react';
import getListaHOC from '../../hoc/ListaHOC';
import ModeloTable from '../../presentational/ModeloTable';
import ModeloForm from '../../presentational/form/ModeloForm';

const busquedad = [
        { nombre : 'Nombre' , type : 'modelos.nombre'},
        { nombre : 'Marca' , type : 'marcas.nombre'},
    ];

const ListaModelo = props =>  (<ModeloTable {...props} />);

export default getListaHOC(ListaModelo,busquedad,{
    uri : '/configuracion/modelo/lista/',
    name : 'Modelo',
    enlace : 'modelo'
},
<ModeloForm />);


