import React from 'react';
import Pop from '../components/Pop';

import containerTableHOC from '../hoc/containerTableHOC';
//  <td>{ e.ubicacion.nombre_departamento + " - " + e.ubicacion.nombre_provincia }</td>
const SucursalTable = props => {
  let datos = props.datos.data || [];
  return (<table className="table">
              <thead className="table_head">
                <tr>
                  <th>Descripción</th>
                  <th>Dirección</th>
                  <th>Tipo</th>
                  <th>Telefono</th>
                  <th>Telefono Aux</th>
                  <th>Zona</th>
                  <th>Ubicación</th>
                  <th></th>
                </tr>
              </thead>
              <tbody className="table_body">
                {datos.map( (e, i) => (
                  <tr key={ 'area_' + i }>
                    <td>{ e.descripcion }</td>
                    <td>{ e.direccion }</td>
                    <td>{ e.tipo }</td>
                    <td>{ e.telefono_1 }</td>
                    <td>{ e.telefono_2 }</td>
                    <td>{ e.zona }</td>
                    <td>{  e.ubicacion.nombre_departamento + " - " + e.ubicacion.nombre_provincia }</td>
                    <td>
                        <div className="acciones">
                          <Pop  name="Más" 
                                color="#607e96"
                                data={[
                                  {enlace : "#", nombre : "Editar"},
                                  {enlace : "#", nombre : "Mostrar"}
                                ]} />
                        </div>
                    </td>
                  </tr>

                ))}
              
              </tbody>
            </table>);
}
      


SucursalTable.defaultProps = {
  datos : []
};

export default containerTableHOC(SucursalTable, 'agencia');


