import React from 'react';
import Pop from '../components/Pop';
import containerTableHOC from '../hoc/containerTableHOC';


const AreaTable = props => {
  let datos = props.datos.data || [];
  return (<table className="table">
              <thead className="table_head">
                <tr>
                  <th>descripcion</th>
                  <th>Telefono</th>
                  <th>Area</th>
                  <th>Agencia</th>
                  <th></th>
                </tr>
              </thead>
              <tbody className="table_body">
                {datos.map( (e, i) => (
                  <tr key={ 'area_' + i }>
                    <td>{ e.descripcion }</td>
                    <td>{ e.telefono }</td>
                    <td>{ e.area }</td>
                    <td>{ e.agencia }</td>
                    <td>
                        <div className="acciones">
                          <Pop  name="Más" 
                                color="#607e96"
                                data={[
                                  {enlace : "#", nombre : "Editar"},
                                  {enlace : "#", nombre : "Mostrar"}
                                ]} />
                        </div>
                    </td>
                  </tr>

                ))}
              
              </tbody>
            </table>
           
            );
}
      


AreaTable.defaultProps = {
  datos : []
};

export default containerTableHOC(AreaTable, 'area');


