import React from 'react';
import Arrow from '../components/Arrow';
import ReactDOM from 'react-dom';

 class Search extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            toogle : false,
            tipo : this.props.busquedad[0].type,
            nombre : this.props.busquedad[0].nombre
        }
    }
    toggleMenu = (e) => {
        e.preventDefault();
        this.setState({
            toogle : this.state.toogle === true ? false : true
        });
    }

    handleType = (e) => {
        e.preventDefault();
        console.log(e.target);
        this.name_buscar.focus();
        this.setState({
            tipo : e.target.type,
            nombre : e.target.id,
            toogle : false
        })
    }
    componentWillMount() {
    // add event listener for clicks
        document.addEventListener('click', this.handleClick, false);
    }

    componentWillUnmount() {
    // make sure you remove the listener when the component is destroyed
        document.removeEventListener('click', this.handleClick, false);
    }

    handleClick = e => {
        if(!ReactDOM.findDOMNode(this).contains(e.target)) {
            this.setState({
                toogle : false
        });
        }
    }

    render(){
    /*  let placeholder =  this.props.busquedad.length !== 1 ? 
                        this.props.busquedad[0].nombre : 
                        this.state.tipo; */
        return (<form action="#" 
                    id={this.state.tipo} 
                    onSubmit={this.props.handleSubmit} className="search">
                <input type="text"
                        name={this.state.tipo}
                        onChange={this.props.handleInput}
                        value={this.props.dato_a_buscar}
                        placeholder={  this.state.nombre}
                        ref={(input) => { this.name_buscar = input; }} 
                        className="search_text"/>
                {this.props.busquedad.length === 1? "" :(
                <li className="search_btn" >
                    <a onClick={this.toggleMenu } className="enlace_color" href="" >
                        <span>{  this.state.nombre}  <Arrow move={this.state.toogle} color="#607e96" border="2px" />     
                        </span>
                    </a>
                    <ul className="lista_search" 
                        style={ { display : this.state.toogle?"block":"none" } }>
                        {this.props.busquedad.map( (e, i) =>(<li  key={"type_" + i} >
                                <a  id={e.nombre}
                                    type={e.type}
                                    onClick={this.handleType} 
                                    className={ e.type === this.state.tipo ? "lista_search_active":""} >
                                    <span>&#10003;</span> {e.nombre}
                                </a>
                            </li>))}
                        

                    </ul>
                </li>)} 
            </form>);
    }
 }


Search.defaultProps = {
  busquedad : []
};
 export default Search;


