import React from 'react';
import Pop from '../components/Pop';
import containerTableHOC from '../hoc/containerTableHOC';


const MarcaTable = props => {
  let datos = props.datos.data || [];
  return (<table className="table">
              <thead className="table_head">
                <tr>
                  <th>Nombre</th>
                  <th>Abreviatura</th>
                  <th></th>
                </tr>
              </thead>
              <tbody className="table_body">
                {datos.map( (e, i) => (
                  <tr key={ 'marca_' + i }>
                    <td>{ e.nombre }</td>
                    <td>{ e.abreviatura }</td>
                    <td>
                        <div className="acciones">
                          <Pop  name="Más" 
                                color="#607e96"
                                data={[
                                  {enlace : "#", nombre : "Editar"},
                                  {enlace : "#", nombre : "Mostrar"}
                                ]} />
                        </div>
                    </td>
                  </tr>

                ))}
              
              </tbody>
            </table>
           
            );
}
      


MarcaTable.defaultProps = {
  datos : []
};

export default containerTableHOC(MarcaTable, 'marca');


