import React from 'react';


class Modal extends React.Component {
    handleIn = e => {
        if(e.target.id === 'modal_in'){
            let bloque = document.getElementById("modal_externo");
            bloque.style.display = 'block';
        }
    }

    handleEscape = e => {
        if(e.target.id === 'modal_externo'){
            let bloque = document.getElementById("modal_externo");
            bloque.style.display = 'none';
        }
    }

    handleCancele = e => {
        let bloque = document.getElementById("modal_externo");
        let modal_form = document.getElementById("modal_form");
        modal_form.reset();
        bloque.style.display = 'none';
    }
    componentWillMount() {
    // add event listener for clicks
        document.addEventListener('keydown', this.handleKeyDown , false);
    }

    componentWillUnmount() {
    // make sure you remove the listener when the component is destroyed
        document.removeEventListener('keydown', this.handleKeyDown, false);
    }
  
    handleKeyDown(e){
        let bloque = document.getElementById("modal_externo");
        if(bloque.style.display === 'block' && e.code === 'Escape'){
            bloque.style.display = "none";
        }
    }
    render(){
        return (<div className="modal_block">
            <span className={this.props.estilos} id="modal_in" onClick={this.handleIn}>Registrar {this.props.nombre}</span>
            <div className="fondo_oscuro" id="modal_externo" onClick={this.handleEscape}>
                <div className="modal">
                    <h1 className="modal_title">
                        Registrar {this.props.nombre} 
                        <span onClick={this.handleEscape}  id="modal_externo" className="modal_close">&#10005;</span></h1>
                        <form className="modal_form" id="modal_form" onSubmit={this.props.handleSubmit}>
                            {this.props.children}
                            
                            <div className="modal_botones">
                                <button  type="submit" className="modal_btn modal_submit" >
                                    <span id="click_cargando">&#8759;</span> Registrar
                                </button>
                                <a href="#" className="modal_btn" onClick={this.handleCancele} title="limpia todo el formulario" >Cancelar</a>
                            </div>
                            
                        </form>
                </div>
            </div>
        </div>)
    }
}

export default Modal;



