

import React from 'react';
import AreaTable from '../../presentational/AreaTable';
import getListaHOC from '../../hoc/ListaHOC';
import AreaForm from '../../presentational/form/AreaForm';
const busquedad = [
        { nombre : 'Descripción' , type : 'areas.descripcion'},
        { nombre : 'Telefono' , type : 'areas.telefono'},
        { nombre : 'Tipo de Area' , type : 'tipo_areas.nombre'},
        { nombre : 'Agencia' , type : 'sucursals.direccion'},
    ];


const ListaArea = props =>  (<AreaTable {...props} />);
export default getListaHOC(ListaArea,busquedad,{
    uri : '/mantenimiento/area/lista/',
    name : 'Area',
    enlace : 'area'
},
<AreaForm />);



