import React from 'react';
import SurcusalTable from '../../presentational/SurcusalTable';
import getListaHOC from '../../hoc/ListaHOC';
import SucursalForm from '../../presentational/form/SucursalForm';

const busquedad = [
        { nombre : 'Descripción' , type : 'sucursals.descripcion'},
        { nombre : 'Dirección' , type : 'sucursals.direccion'},
        { nombre : 'Tipo' , type : 'sucursals.tipo'},
        { nombre : 'Telefono' , type : 'sucursals.telefono_1'},
        { nombre : 'Zona' , type : 'zonas.nombre'},
    ];


const ListaSucursal = props =>  (<SurcusalTable {...props} />);
export default getListaHOC(ListaSucursal,busquedad,{
    uri : '/mantenimiento/agencia/lista/',
    name : 'Agencia',
    enlace : 'agencia'
},
<SucursalForm />);



