import React from 'react';


const ModeloForm = props => {
    return (
        <div className="form_component ">
            <div className="form_text">
                <label htmlFor="">Modelo</label>
                <input className="input_modal" type="text" placeholder="Modelo" name="nombre" id="nombre"/>
            </div>
            <div className="form_text">
                <label htmlFor="">Marca</label>
                <input className="input_modal" type="text" placeholder="Marca" name="marca_id" id="marca_id" />                            
            </div>
        </div>
    );
}

export default ModeloForm;


