import React from 'react';
import MarcaTable from '../../presentational/MarcaTable';
import MarcaForm from '../../presentational/form/MarcaForm';
import getListaHOC from '../../hoc/ListaHOC';
const busquedad = [
        { nombre : 'Nombre' , type : 'nombre'},
        { nombre : 'Abreviatura' , type : 'abreviatura'}
    ];

const ListaMarca = props =>  (<MarcaTable {...props} />);

export default getListaHOC(ListaMarca,busquedad,{
    uri : '/configuracion/marca/lista/',
    name : 'Marca',
    enlace : 'marca'
},
<MarcaForm />);


