

import React from 'react';
import ZonaTable from '../../presentational/ZonaTable';
import getListaHOC from '../../hoc/ListaHOC';
import ZonaForm from '../../presentational/form/ZonaForm';

const busquedad = [
        { nombre : 'Nombre' , type : 'nombre'},
        { nombre : 'Descripción' , type : 'descripcion'}
    ];




const ListaZona = props =>  (<ZonaTable {...props} />);
export default getListaHOC(ListaZona,busquedad,{
    uri : '/configuracion/zona/lista/',
    name : 'Zona',
    enlace : 'zona'
},
<ZonaForm />);



