import React from 'react';


const AreaForm = props => {
    return (
        <div className="form_component grilla_doble">
            <div className="form_text">
                <label htmlFor="">Descripción</label>
                <input className="input_modal" type="text" placeholder="Descripción" name="descripcion"  id="descripcion"/>
            </div>
            <div className="form_text">
                <label htmlFor="">Telefono</label>
                <input className="input_modal" type="text" placeholder="Telefono" name="telefono" id="telefono"/>                            
            </div>
            <div className="form_text">
                <label htmlFor="">Tipo de Area</label>
                <input className="input_modal" type="text" placeholder="Area" name="tipo_area_id"  id="tipo_area_id"/>
            </div>
            <div className="form_text">
                <label htmlFor="">Agencia</label>
                <input className="input_modal" type="text" placeholder="Agencia" name="sucursal_id" id="sucursal_id" />                            
            </div>
        </div>
    );
}

export default AreaForm;


