import React from 'react';
import { Link } from 'react-router'

class Pagination extends React.Component {
    render() {
        if(this.props.cantidad === 1 || this.props.cantidad === 0 ){
            return (<span></span>);
        }
        return (<ul className="pagination">
                <li> 
                    <Link to={this.props.uri + ( this.props.actual - 1) } 
                    className={ this.props.actual === 1 ? "page_disabled" : ""}> {"<"} </Link> 
                </li>
                {[...Array(this.props.cantidad).keys()].map( (e,i) =>(
                    <li key={"page_" + i}> 
                        <Link to={this.props.uri + (e + 1) }
                             className={ this.props.actual === e + 1 ? "page_active":""}
                              activeClassName="page_active">{e + 1}</Link>
                    </li>
                ))}
                <li> 
                    <Link to={this.props.uri + ( this.props.actual + 1) } 
                    className={ this.props.cantidad === this.props.actual ? "page_disabled" : ""}> {">"} </Link> 
                </li>
            </ul>);
    }
}

export default Pagination;


