import React from 'react';
import getListaHOC from '../../hoc/ListaHOC';
import TipoEquipoTable from '../../presentational/TipoEquipoTable';
import TipoEquipoForm from '../../presentational/form/TipoEquipoForm';

const busquedad = [
        { nombre : 'Nombre' , type : 'nombre'},
        { nombre : 'Abreviatura' , type : 'abreviatura'}
    ];




const ListaTipoEquipo = props =>  (<TipoEquipoTable {...props} />);
export default getListaHOC(ListaTipoEquipo,busquedad,{
    uri : '/configuracion/tipo-equipo/lista/',
    name : 'Tipo Equipo',
    enlace : 'tipo-equipo'
},
<TipoEquipoForm />);



