import React from 'react';


const ModeloForm = props => {
    return (
        <div className="form_component grilla_doble">
            <div className="form_text">
                <label htmlFor="">Razón Social</label>
                <input className="input_modal" type="text" placeholder="Razón Social" name="razon_social" id="razon_social"/>
            </div>
            <div className="form_text">
                <label htmlFor="">Dirección</label>
                <input className="input_modal" type="text" placeholder="Dirección" name="direccion" id="direccion" />                            
            </div>
            <div className="form_text">
                <label htmlFor="">Email</label>
                <input className="input_modal" type="text" placeholder="Email" name="email" id="email"/>
            </div>
            <div className="form_text">
                <label htmlFor="">Contacto</label>
                <input className="input_modal" type="text" placeholder="Contacto" name="contacto" id="contacto" />                            
            </div>
            <div className="form_text">
                <label htmlFor="">Telefono</label>
                <input className="input_modal" type="text" placeholder="Telefono" name="telefono_1" id="telefono_1"/>
            </div>
            <div className="form_text">
                <label htmlFor="">Telefono aux</label>
                <input className="input_modal" type="text" placeholder="Telefono aux" name="telefono_2" id="telefono_2" />                            
            </div>
            <div className="form_text">
                <label htmlFor="">Telefono aux</label>
                <input className="input_modal" type="text" placeholder="Telefono aux" name="telefono_3" id="telefono_3"/>
            </div>
            
        </div>
    );
}

export default ModeloForm;


