import React from 'react';


let upDivArrow = {
    position: 'relative',
    width: 12,
    height: 13,
    display: 'inline-block',
    overflow: 'hidden',
    marginLeft: 11,
    transform: 'translateY(40%)'
};
let downDivArrow = {
    position: 'relative',
    width: 12,
    display: 'inline-block',
    height: 13,
    overflow: 'hidden',
    marginLeft: 11,
    transform : 'translateY(-20%) rotate(180deg)'
};
const upArrow = (color, border) => ({
    height: 10,
    border: border + ' solid ' + color,
    display: 'block',
    transform: 'rotate(45deg) translateX(-50%) translateY(-50%)'

});
//let upArrow = 

const Arrow = (props) =>  (
    <div style={ props.move ? downDivArrow : upDivArrow} >
        <span style={upArrow(props.color, props.border)}></span>
    </div>  
    ); 
    
Arrow.defaultProps = {
  color: '#969595',
  border: '1px'
};


export default Arrow;


