import React from 'react';
import $ from 'jquery'
import Pagination from '../components/Pagination';
import Mensaje from '../components/Mensaje';

function getDataReu(TablaContainer, uri){
    return class extends React.Component {
            constructor() {
                super();
                this.state = { 
                    data: []
                }
            }
            componentDidMount() {
                $.ajax({
                    url: "http://localhost/~vimcemt/inventario/public/index.php/" + 
                                                                    uri + 
                                                                    "?page=" + 
                                                                    this.props.page,
                    async: false,
                    dataType: 'json',
                    success: function(data) {
                        this.setState({data: data});
                    }.bind(this)
                });
            }
            componentWillReceiveProps(nextProps) {
                if (this.props.page !== nextProps.page || 
                        this.props.search !== nextProps.search ||
                        nextProps.estado === true) {
                    let pagina = this.props.search !== nextProps.search ? 1 :nextProps.page;
                    
                    $.ajax({
                        url: "http://localhost/~vimcemt/inventario/public/index.php/" + 
                                                                        uri + 
                                                                        "?page=" + 
                                                                         pagina
                                                                        + nextProps.search,
                        async: true,
                        dataType: 'json',
                        success: function(data) {
                            this.setState({data: data});
                        }.bind(this)
                    });
                }
            }



            render(){
                return (
                    <div>
                        <Mensaje onClick={this.props.onClickMensaje} 
                                 cantidad={this.state.data.total}
                                 dato={this.props.dato} />
                        <TablaContainer datos={this.state.data}  />
                        <Pagination cantidad={this.state.data.last_page}
                                    actual={this.state.data.current_page}
                                    uri={this.props.uri} />
                    </div>
                );
            }
        }

}
getDataReu.defaultProps = {
    page : 1,
    search : ""
};

export default getDataReu;


