import React from 'react';
import getListaHOC from '../../hoc/ListaHOC';
import TipoAreaTable from '../../presentational/TipoAreaTable';
import TipoAreaForm from '../../presentational/form/TipoAreaForm';

const busquedad = [
        { nombre : 'Nombre' , type : 'nombre'}
    ];

const ListaTipoArea = props =>  (<TipoAreaTable {...props} />);

export default getListaHOC(ListaTipoArea,busquedad,{
    uri : '/configuracion/tipo-area/lista/',
    name : 'Tipo Area',
    enlace : 'tipo-area'
},
<TipoAreaForm />);



