import React from 'react';
import {ICONS} from '../constantes/ICON';
import Pop from '../components/Pop';


const notify = [
    { enlace :'#', nombre : '120 maquinas registradas'},
    { enlace :'#', nombre : '3 maquinas reparadas'},
    { enlace :'#', nombre : '3 nuevos contratos'}  
];

const cuenta = [
    { enlace : '#', nombre : 'Configuración de Cuenta'},
    { enlace : '#', nombre : 'Salir'}
];

class User extends React.Component{
    render(){
        return (<div className="header_cuenta">
                
                <Pop
                    icon={ICONS.CAMPANA}
                    name_modal="Notificaciones"
                    data={notify}
                    color="#607e96"
                    border='2px'
                    width='250px'
                />
                
                <Pop
                    icon={ICONS.USER}
                    name_modal="Christian Benavides"
                    data={cuenta}
                    color="#607e96"
                    border='2px'
                    width='200px'
                />
            </div>);
    }
}

export default User;


