import React from 'react';
import Arrow from '../components/Arrow';

class EnlaceMenu extends React.Component {

    render() {
        let bloque = this.props.toogle === this.props.name  ?  "block": "none";
        let activar_icon = this.props.toogle === this.props.name ? true : false;
        return (
            <li>
                  <a onClick={this.props.onClick } href="#">{this.props.name}
                      <Arrow move={activar_icon} color="#fff" border="1px"/>         
                  </a>
                  <ul className="sub_list"  style={{'display' : bloque }}>  
                     { this.props.children }
                  </ul>
            </li>
        );
    }
}

export default EnlaceMenu;


