import React from 'react';


const ZonaForm = props => {
    return (
        <div className="form_component">
            <div className="form_text">
                <label htmlFor="">Nombre</label>
                <input className="input_modal" type="text" placeholder="Nombre" name="nombre" />
            </div>
            <div className="form_text">
                <label htmlFor="">Descripción</label>
                <input className="input_modal" type="text" placeholder="Descripción" name="descripcion" />                            
            </div>
        </div>
    );
}

export default ZonaForm;


