import React from 'react';
import Pop from '../components/Pop';
import containerTableHOC from '../hoc/containerTableHOC';


const EquipoTable = props => {
  let datos = props.datos.data || [];
  return (<table className="table">
              <thead className="table_head">
                <tr>
                  <th>Serie</th>
                  <th>Estado Equipo</th>
                  <th>Proveedor</th>
                  <th>Tipo Equipo</th>
                  <th>Modelo</th>
                  <th></th>
                </tr>
              </thead>
              <tbody className="table_body">
                {datos.map( (e, i) => (
                  <tr key={ 'equipo_' + i }>
                    <td>{ e.serie }</td>
                    <td>{ e.estado_equipo }</td>
                    <td>{ e.mombre_proveedor }</td>
                    <td>{ e.nombre_tipo_equipo }</td>
                    <td>{ e.nombre_modelo }</td>
                    <td>
                        <div className="acciones">
                          <Pop  name="Más" 
                                color="#607e96"
                                data={[
                                  {enlace : "#", nombre : "Editar"},
                                  {enlace : "#", nombre : "Mostrar"}
                                ]} />
                        </div>
                    </td>
                  </tr>

                ))}
              
              </tbody>
            </table>
           
            );
}
      


EquipoTable.defaultProps = {
  datos : []
};

export default containerTableHOC(EquipoTable, 'equipo');


