import React from 'react';

class ModalPop extends React.Component{
    render(){
        return(
                    <div style={{ "display" : this.props.toogle ? 'block' : 'none' }}>
                        <div className="arrow-up arrow_encima"></div>
                        <div className="arrow-up arrow_abajo"></div>
                        <ul className="pop" style={{ 'width' : this.props.width }}>
                            { this.props.name_modal ?(
                                <li>
                                    <h5 href="#" className="header_pop"> {this.props.name_modal}</h5>
                                </li>):''  }
                   
                            { this.props.data.map(  (e, i) =>
                                <li key={"nombre_" + i}>
                                    <a href={e.enlace} >{e.nombre}</a>
                                </li>
                            )}
                        </ul>                    
                    </div>
        );
    }
}

ModalPop.defaultProps = {
  width : '120px'
};

export default ModalPop;


