import React from 'react';
import ProveedorTable from '../../presentational/ProveedorTable';
import getListaHOC from '../../hoc/ListaHOC';
import ProveedorForm from '../../presentational/form/ProveedorForm';

const busquedad = [
        { nombre : 'Razon Social' , type : 'razon_social'},
        { nombre : 'Dirección' , type : 'direccion'},
        { nombre : 'Email' , type : 'email'},
        { nombre : 'Contacto' , type : 'contacto'},
    ];


const ListaProveedor = props =>  (<ProveedorTable {...props} />);
export default getListaHOC(ListaProveedor,busquedad,{
    uri : '/mantenimiento/proveedor/lista/',
    name : 'Proveedor',
    enlace : 'proveedor'
},
<ProveedorForm />);



