import React from 'react';


const SucursalForm = props => {
    return (
        <div className="form_component grilla_doble">
            <div className="form_text">
                <label htmlFor="">Descripción</label>
                <input className="input_modal" type="text" placeholder="Descripción" name="descripcion" id="descripcion"/>
            </div>
            <div className="form_text">
                <label htmlFor="">Dirección</label>
                <input className="input_modal" type="text" placeholder="Dirección" name="direccion" id="direccion" />                            
            </div>
            <div className="form_text">
                <label htmlFor="">Tipo</label>
                <input className="input_modal" type="text" placeholder="Tipo" name="tipo" id="tipo"/>
            </div>
            <div className="form_text">
                <label htmlFor="">Telefono</label>
                <input className="input_modal" type="text" placeholder="Telefono" name="telefono_1" id="telefono_1" />                            
            </div>            
            <div className="form_text">
                <label htmlFor="">Telefono aux</label>
                <input className="input_modal" type="text" placeholder="Telefono aux" name="telefono_2" id="telefono_2" />                            
            </div>
            <div className="form_text">
                <label htmlFor="">Departamento</label>
                <input className="input_modal" type="text" placeholder="Departamento" name="codigo_dep" id="codigo_dep"/>
            </div>
            <div className="form_text">
                <label htmlFor="">Provincia</label>
                <input className="input_modal" type="text" placeholder="Provincia" name="codigo_pro" id="codigo_pro"/>
            </div>
            <div className="form_text">
                <label htmlFor="">Distrito</label>
                <input className="input_modal" type="text" placeholder="Distrito" name="codigo_dis" id="codigo_dis"/>
            </div>
            <div className="form_text">
                <label htmlFor="">Zona</label>
                <input className="input_modal" type="text" placeholder="Zona" name="zona_id" id="zona_id"/>
            </div>
            
        </div>
    );
}

export default SucursalForm;


