import React, { Component } from 'react';
import Menu from './layouts/Menu';
import User from './layouts/User';
import './App.css';
/*


 */ 
class App extends Component {
  render() {
    return (
      <section className="flex_body">
        <aside className="flex_aside">
          <Menu />
        </aside> 
        <div className="flex_div">
            <User />
            {this.props.children}
        </div>
      
      </section>
    );
  }
}

export default App;



