import React from 'react';
import { Router, Route, IndexRoute } from 'react-router';

import App from '../App';
import Principal from './Principal';

// mantenimiento
import Equipo from './mantenimiento/Equipo';
import Proveedor from './mantenimiento/Proveedor';
import Area from './mantenimiento/Area';
import Sucursal from './mantenimiento/Surcursal';
import ListaEquipo from './mantenimiento/ListaEquipo';
import ListaProveedor from './mantenimiento/ListaProveedor';
import ListaArea from './mantenimiento/ListaArea';
import ListaSucursal from './mantenimiento/ListaSucursal';
// configuraciones
import Marca from './configuracion/Marca';
import Modelo  from './configuracion/Modelo';
import TipoArea from './configuracion/TipoArea';
import TipoEquipo from './configuracion/TipoEquipo';
import Zona from './configuracion/Zona';
import ListaMarca from './configuracion/ListaMarca';
import ListaModelo from './configuracion/ListaModelo';
import ListaTipoArea from './configuracion/ListaTipoArea';
import ListaTipoEquipo from './configuracion/ListaTipoEquipo';
import ListaZona from './configuracion/ListaZona';


const Routes = (props) => (
  <Router {...props}>
    <Route path="/" component={App}>
      <IndexRoute component={Principal}/>

      <Route path="/mantenimiento/equipo" component={Equipo}>
        <IndexRoute component={ListaEquipo}/>
        <Route path="/mantenimiento/equipo/lista/:page" component={ListaEquipo} />
      </Route>
      <Route path="/mantenimiento/proveedor" component={Proveedor}>
        <IndexRoute component={ListaProveedor}/>
        <Route path="/mantenimiento/proveedor/lista/:page" component={ListaProveedor} />
      </Route>
      <Route path="/mantenimiento/area" component={Area}>
        <IndexRoute component={ListaArea}/>
        <Route path="/mantenimiento/area/lista/:page" component={ListaArea} />
      </Route>
      <Route path="/mantenimiento/agencia" component={Sucursal}>
        <IndexRoute component={ListaSucursal}/>
        <Route path="/mantenimiento/agencia/lista/:page" component={ListaSucursal} />
      </Route>
      

      <Route path="/configuracion/marca" component={Marca}>
        <IndexRoute component={ListaMarca}/>
        <Route path="/configuracion/marca/lista/:page" component={ListaMarca} />
      </Route>
      <Route  path="/configuracion/modelo" component={Modelo}>
        <IndexRoute component={ListaModelo}/>
        <Route path="/configuracion/modelo/lista/:page" component={ListaModelo} />
      </Route>
      <Route path="/configuracion/tipo-area" component={TipoArea}>
        <IndexRoute component={ListaTipoArea}/>
        <Route path="/configuracion/tipo-area/lista/:page" component={ListaTipoArea} />
      </Route>
      <Route path="/configuracion/tipo-equipo" component={TipoEquipo}>        
        <IndexRoute component={ListaTipoEquipo}/>
        <Route path="/configuracion/tipo-equipo/lista/:page" component={ListaTipoEquipo} />
      </Route>
      <Route path="/configuracion/zona" component={Zona}>        
        <IndexRoute component={ListaZona}/>
        <Route path="/configuracion/zona/lista/:page" component={ListaZona} />
      </Route>    
    </Route>
  </Router>
);

export default Routes;


