import React from 'react';
import $ from 'jquery'

import Search from '../components/Search';
import {browserHistory} from 'react-router';
import Modal from '../components/Modal';

function getListaHOC(TipoTable,busquedad,data,form){
    return class  extends React.Component{
        constructor(props){
            super(props);
            this.state = {
                search : "",
                dato : "",
                dato_a_buscar: "",
                estado : false
            }
        }
        handleSubmit = (e) => {
            e.preventDefault();
            browserHistory.push(data.uri + '1');
            let tipo = busquedad.length === 1 ? busquedad[0].type : e.target.id;
            this.setState({
                search : "&type="+ tipo +"&dato=" + e.target.elements[tipo].value,
                dato :  e.target.elements[tipo].value,
            //   pagina : 1
            })
        }

        onClickMensaje = (e) => {
            e.preventDefault();
            browserHistory.push(data.uri + '1');
            this.setState({
                search : "",
                dato : "",
                dato_a_buscar : "",
            })
        }
        handleInput = (e) => {
            this.setState({
                dato_a_buscar : e.target.value
            })
        }

        handleSubmitForm = (e) => {
            e.preventDefault();
            let ambito = this;
            let click_cargando = document.getElementById("click_cargando");
            let bloque = document.getElementById("modal_externo");
            click_cargando.style.display = "block";
            $.ajax({
                type: "POST",
                url: "http://localhost/~vimcemt/inventario/public/index.php/" + data.enlace,
                data: $(e.target).serialize(),
                dataType: "json",
                async : true,
                success: function(response) {
                    browserHistory.push(data.uri + '1');
                    click_cargando.style.display = "none";
                    bloque.style.display = "none";
                    console.log("delay");
                    ambito.setState({
                        search : "",
                        dato : "",
                        dato_a_buscar : "",
                        estado : true
                    });
                },
                error: function() {
                    console.log('error handing here');
                }
            });
        }
        render() {
            let page = this.props.params.page || 1; 
            // <a href="#" className="btn-blue">Registrar {data.name}</a>
            return (<div>
                    <div className="flex_bet espacios_27">
                        <Search handleSubmit={this.handleSubmit}  
                                busquedad={busquedad}
                                handleInput={this.handleInput}
                                dato_a_buscar={this.state.dato_a_buscar}/>
                        <div>
                            <Modal handleSubmit={this.handleSubmitForm} nombre={data.name}  estilos="btn-blue" >
                                {form}
                            </Modal>            
                        </div>
                    </div>
            
                    <TipoTable  page={ page } 
                                onClickMensaje={this.onClickMensaje}
                                dato={this.state.dato}
                                estado={this.state.estado}
                                uri={data.uri}
                                search={this.state.search} 
                                />    
                    </div>);
        }

    }

}

export default getListaHOC;


