import React from 'react';
import Tabs from './Tabs';
class Mantenimiento extends React.Component{
    render(){
        return (
            <section className="maestro_max">
                <h1 className="head_name espacios_22">
                    {this.props.name}
                </h1>
               
                <Tabs tabs={this.props.tabs}/>
                { this.props.children }

            </section>
        );
    }
}

export default Mantenimiento;

