import React from 'react';
import Pop from '../components/Pop';
import containerTableHOC from '../hoc/containerTableHOC';

const ProveedorTable = props => {
  let datos = props.datos.data || [];
  return (<table className="table">
              <thead className="table_head">
                <tr>
                  <th>Razon Social</th>
                  <th>Dirección</th>
                  <th>Email</th>
                  <th>Contacto</th>
                  <th></th>
                </tr>
              </thead>
              <tbody className="table_body">
                {datos.map( (e, i) => (
                  <tr key={ 'marca_' + i }>
                    <td>{ e.razon_social }</td>
                    <td>{ e.direccion }</td>
                    <td>{ e.email }</td>
                    <td>{ e.contacto }</td>
                    <td>
                        <div className="acciones">
                          <Pop  name="Más" 
                                color="#607e96"
                                data={[
                                  {enlace : "#", nombre : "Editar"},
                                  {enlace : "#", nombre : "Mostrar"}
                                ]} />
                        </div>
                    </td>
                  </tr>

                ))}
              
              </tbody>
            </table>);
}
      


ProveedorTable.defaultProps = {
  datos : []
};

export default containerTableHOC(ProveedorTable, 'proveedor');


