import React from 'react';

class Tabs extends React.Component {
    render(){
        return (<ul className="tab">
            {this.props.tabs.map( (e,i) => (
              <li key={ "tab_" + i}>
                <a  className={ e.active ? 'active_tab' : ''}  href={e.enlace} > { e.nombre }</a>
              </li>
            ))}
          </ul>);
    }
}
Tabs.defaultProps = {
  tabs : []
};


export default Tabs;
