import React from 'react';

const Mensaje = props => {

    return (<p className="letra_p pdb_15">
    {props.cantidad} 
    {props.cantidad === 1?" registro se ha encontrado ":
                    " registros se han encontrado "}
    {props.dato ?
        <span>que coindiciden con {props.dato + " - "}  
         <a onClick={props.onClick} className="color_blue_enlace" >ver todos</a>
    </span>: ''}
</p>)};

export default Mensaje;


