import React from 'react';
import EnlaceMenu from './EnlaceMenu';
import { Link } from 'react-router'

class Menu extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            text : ""
        }
    }
    toggleMenu = (e) => {
        this.setState({
            text : e.currentTarget.text === this.state.text ? "" : e.currentTarget.text
        });

    }
    render(){

         return (
            <nav className="header_nav">
              <div className="short_logo logo_aside"> 
                <img src="https://cloud-cdn-digitalocean-com.global.ssl.fastly.net/aurora/assets/images/cloud-logo-65198dc5bebec93d2bf95cfacb7b4eed.svg" alt=""/>
                <h3>SISGIEC</h3>
              </div>
              <ul className="header_menu" >
                <li>
               
                     <Link to="/" activeClassName="link_active"  onlyActiveOnIndex={true}>
                                 Principal
                     </Link>
                </li>
                <EnlaceMenu onClick={this.toggleMenu }  
                            toogle={this.state.text}
                            name="Operaciones">
                    <li>
                        <a href="#"><span>&#8211;</span> Asignar Equipo</a>
                    </li>
                    <li>
                        <a href="#"><span>&#8211;</span> Crear Contrato</a>
                    </li>
                    <li>
                        <a href="#"><span>&#8211;</span> Seg. de equipo</a>
                    </li>
                </EnlaceMenu>
                <EnlaceMenu onClick={this.toggleMenu }  
                            toogle={this.state.text}
                            name="Mantenimiento">
                    <li>
                        <Link to="/mantenimiento/equipo" activeClassName="link_active">
                                <span>&#8211;</span>  Equipo
                        </Link>
                    </li>
                    <li>
                        <Link to="/mantenimiento/proveedor" activeClassName="link_active">
                                <span>&#8211;</span>  Proveedor
                        </Link>
                    </li>
                    <li>
                        <Link to="/mantenimiento/agencia" activeClassName="link_active">
                                <span>&#8211;</span>  Agencia
                        </Link>
                    </li>
                    <li>
                        <Link to="/mantenimiento/area" activeClassName="link_active">
                                <span>&#8211;</span>  Area
                        </Link>
                    </li>
                    
                </EnlaceMenu>
                <EnlaceMenu onClick={this.toggleMenu }  
                            toogle={this.state.text}
                            name="Configuración">
                    <li>
                        <Link to="/configuracion/marca" activeClassName="link_active">
                                <span>&#8211;</span>  Marca
                        </Link>
                    </li>
                    <li>
                       <Link to="/configuracion/modelo" activeClassName="link_active">
                                <span>&#8211;</span>  Modelo
                        </Link>
                    </li>
                    <li>
                        <Link to="/configuracion/tipo-area" activeClassName="link_active">
                                <span>&#8211;</span>  Tipo Area
                        </Link>
                    </li>
                    <li>
                        <Link to="/configuracion/tipo-equipo"  activeClassName="link_active">
                                <span>&#8211;</span>  Tipo Equipo
                        </Link>
                    </li>
                    <li>
                        <Link to="/configuracion/zona"  activeClassName="link_active">
                                <span>&#8211;</span>  Zona
                        </Link>
                    </li>
                </EnlaceMenu>
                <EnlaceMenu onClick={this.toggleMenu }  
                             toogle={this.state.text}
                            name="Imp. por lote">
                    <li>
                        <a href="#"><span>&#8211;</span> Asignar Equipo</a>
                    </li>
                    <li>
                        <a href="#"><span>&#8211;</span> Crear Contrato</a>
                    </li>
                    <li>
                        <a href="#"><span>&#8211;</span> Seg. de equipo</a>
                    </li>
                </EnlaceMenu>
                <EnlaceMenu onClick={this.toggleMenu }  
                            toogle={this.state.text}
                            name="Reportes">
                    <li>
                        <a href="#"><span>&#8211;</span> Asignar Equipo</a>
                    </li>
                    <li>
                        <a href="#"><span>&#8211;</span> Crear Contrato</a>
                    </li>
                    <li>
                        <a href="#"><span>&#8211;</span> Seg. de equipo</a>
                    </li>
                </EnlaceMenu>
                <li>
                  <a  href="#">HelpDesk</a>
                </li>
              </ul>
            </nav>
         )
    }
}

export default Menu;


