import React from 'react';


const EquipoForm = props => {
    return (
        <div className="form_component grilla_doble">
            <div className="form_text">
                <label htmlFor="">Serie</label>
                <input className="input_modal" type="text" placeholder="Serie" name="serie" id="serie"/>
            </div>
            <div className="form_text">
                <label htmlFor="">Estado Equipo</label>
                <input className="input_modal" type="text" placeholder="Estado Equipo" name="estado_equipo" id="estado_equipo"/>
            </div>
            <div className="form_text">
                <label htmlFor="">Proveedor</label>
                <input className="input_modal" type="text" placeholder="Proveedor" name="proveedor_id" id="proveedor_id"/>
            </div>
            <div className="form_text">
                <label htmlFor="">Tipo de Equipo</label>
                <input className="input_modal" type="text" placeholder="Tipo de Equipo" name="tipo_equipo_id" id="tipo_equipo_id"/>
            </div>
            <div className="form_text">
                <label htmlFor="">Modelo</label>
                <input className="input_modal" type="text" placeholder="Modelo" name="modelo_id" id="modelo_id"/>
            </div>
        </div>
    );
}

export default EquipoForm;


