import React from 'react';
import ReactDOM from 'react-dom';
import Arrow from '../components/Arrow';
import ModalPop from './ModalPop';
import Icon from '../components/Icon';

class Pop extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            toogle : false
        }
    }

    toggleMenu = () => {
        this.setState({
            toogle : this.state.toogle === true ? false : true,
            campana :false
        });
    }

    componentWillMount() {
    // add event listener for clicks
        document.addEventListener('click', this.handleClick, false);
    }

    componentWillUnmount() {
    // make sure you remove the listener when the component is destroyed
        document.removeEventListener('click', this.handleClick, false);
    }
    
    handleClick = e => {
        if(!ReactDOM.findDOMNode(this).contains(e.target)) {
             this.setState({
                toogle : false
        });
        }
    }

    render(){
        return (
         <div className="capa_pop">
                    <div onClick={this.toggleMenu } className="flex_center">
                        <span style={{
                            color : this.props.color
                        }}>
                        { !this.props.icon ?
                            this.props.name :
                            <Icon icon={this.props.icon} color={this.props.color} />         
                        }
                        </span>
                        <Arrow move={this.state.toogle}  
                                color={this.props.color}
                                border={this.props.border}
                                width={this.props.width} />               
                    </div>  
                    <ModalPop toogle={this.state.toogle} 
                              name_modal={this.props.name_modal} 
                              data={this.props.data }
                              width={this.props.width} />            
          </div>);
    }
}

Pop.defaultProps = {
  color:  '#969595',
  border: '1px',
  icon : false,
  data : []
};

export default Pop;


