import React from 'react';


const MarcaForm = props => {
    return (
        <div className="form_component ">
            <div className="form_text">
                <label htmlFor="">Nombre</label>
                <input className="input_modal" type="text" placeholder="Nombre" name="nombre" />
            </div>
            <div className="form_text">
                <label htmlFor="">Abreviatura</label>
                <input className="input_modal" type="text" placeholder="Abreviatura" name="abreviatura" />                            
            </div>
        </div>
    );
}

export default MarcaForm;


